<template>
  <img alt="Vue logo" src="./assets/p1.png" width="300">
  <h1>{{ firstname }}  {{ lastname }}</h1> 
  <button v-on:click="showalert">แสดงชื่อ</button>
  <button v-on:click="showalert1">แสดงสกุล</button>
  <h1>{{ age }}</h1>
  <button @click="plus">เพิ่มตัวเลข</button>
  <button @click="del">ลบตัวเลข</button>
  <button @click="plus5">เพิ่มตัวเลข5</button>
  <button @click.right="del5">ลบตัวเลข5</button>
  <form action="">
    <label for="">กรุณากรอกชื่อเล่น :</label>
    <input type="text" @input="setnickname">
    <br>
    <label for="">กรุณากรอกชื่อจริง :</label>
    <input type="text" @input="setfname">
    <br>
    <label for="">กรุณากรอกนามสกุล :</label>
    <input type="text" @input="setlname">
    <br>
    <label for="">กรุณากรอกอายุ :</label>
    <input type="text" @input="setag">
    <br>
    <label for="">กรุณากรอกวัน/เดือน/ปีเกิด :</label>
    <input type="text" @input="setdate">
    <br>
    <label for="">กรุณากรอกสาขา :</label>
    <input type="text" @input="setmajor">
    <br>
    <label for="">กรุณากรอกห้อง :</label>
    <input type="text" @input="setcl">
    <br>
    <label for="">กรุณากรอกคติประจำใจ :</label>
    <input type="text" @input="setkit1">
    <br>
    <label for="">กรุณากรอกสถานที่เรียนต่อ :</label>
    <input type="text" @input="setkit2">
    <br>
    <label for="">กรุณากรอกวิชาที่ไม่ชอบ :</label>
    <input type="text" @input="setkit3">
    
  </form>
  <h2>แสดงชื่อเล่น :{{ nickname }}</h2>
  <h2>แสดงชื่อจริง :{{ fname }}</h2>
  <h2>แสดงนามสกุล :{{ lname }}</h2>
  <h2>แสดงอายุ :{{ ag }}</h2>
  <h2>แสดงวัน/เดือน/ปีเกิด :{{ date }}</h2>
  <h2>แสดงสาขา :{{ major }}</h2>
  <h2>แสดงห้อง :{{ cl }}</h2>
  <h2>แสดงคติประจำใจ :{{ kit1 }}</h2>
  <h2>แสดงสถานที่เรียนต่อ :{{ kit2 }}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ :{{ kit3 }}</h2>
</template>

<script>


export default {
  name: 'App',
    data(){
      return{
        firstname: "Kittayoch",
        lastname: "Ongun",
        age: 18,
        nickname: "",
        fname: "",
        lname:"",
        ag:"",
        date:"",
        major:"",
        cl:"",
        kit1:"",
        kit2:"",
        kit3:""

      }
    },
    methods:{
      showalert(){
        alert(this.firstname)
      },
      showalert1(){
        alert(this.lastname)
      },
      plus(){
        this.age++
      },
      del(){
        this.age--
      },
      plus5(){
        this.age+=5
      },
      del5(){
        this.age-=5
      },
      setnickname(event){
        this.nickname =event.target.value
      },
      setfname(event){
        this.fname =event.target.value
      },
      setlname(event){
        this.lname =event.target.value
      },
      setag(event){
        this.ag =event.target.value
      },
      setdate(event){
        this.date =event.target.value
      },
      setmajor(event){
        this.major =event.target.value
      },
      setcl(event){
        this.cl =event.target.value
      },
      setkit1(event){
        this.kit1 =event.target.value
      },
      setkit2(event){
        this.kit2 =event.target.value
      },
      setkit3(event){
        this.kit3 =event.target.value
      },
    }

}
</script>

